 <template>
  <div>
    <Header>
      <template v-slot:title>
        <div class="tab-container">
          <DropdownMenu active-color="#2878FF">
            <DropdownItem v-model="fundType"
                          :options="fundTypeOptions" />
          </DropdownMenu>
        </div>
      </template>
    </Header>
    <!-- <div class="">骨架屏</div> -->
    <div class="content">
      <div class="menubar">
        <div class="left">
          <DropdownMenu active-color="#2878FF">
            <!-- <DropdownItem v-model="dateType"
                          :options="timeOptions" /> -->
            <DropdownItem :title="dateTypeTitle"
                          ref="item">
              <van-cell v-for="(item,index) in timeOptions"
                        center
                        :title="item.text"
                        :key='index'
                        :style="{'color':item.selected?'#2878FF':'#000'}"
                        @click="onConfirmDateType(index,item)">
                <template #right-icon
                          v-if="item.selected">
                  <van-icon name="success"
                            color="#2878FF" />
                </template>
              </van-cell>
            </DropdownItem>
            <DropdownItem v-if='fundType!==4 && fundType!==5'
                          v-model="fundState"
                          :options="fundStateOptions" />
          </DropdownMenu>
        </div>
        <div class="right">
          <span>收入 {{(totalComminsion.totalComminsionIn.toFixed(2))}}</span>
          <span>支出 {{totalComminsion.totalComminsionOut.toFixed(2)}}</span>
        </div>
      </div>
      <div class="list"
           v-if="dataList.length > 0">
        <!--上拉加载-->
        <van-list v-model="loading"
                  :finished="loadFinished"
                  :finishedText="'暂无更多数据'"
                  @load="onLoad"
                  :immediate-check="false">
          <div class="item"
               v-for="(item) in dataList"
               :key="item.billId">
            <div class="left">
              <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/sales/default.png"
                   alt="">
            </div>
            <div class="mid">
              <span class="name">{{item.productName || "未知商品"}}</span>
              <span class="msg">{{item | stateText}}</span>
              <span class="time">{{item.transactionTime || ""}}</span>
            </div>
            <div class="right">
              <span :style="{'color':item.forb === '1'?'#ff6026':'#000'}">{{item | feePrice}}</span>
            </div>
          </div>
        </van-list>
      </div>
      <div class='no-date'
           v-else>
        <img :src="`${$cdnImageUrl}/mine/sales/quesheng.png`"
             alt="">
        <span>暂无账单</span>
      </div>
      <!-- 弹出层 -->
      <van-popup v-model="showPopup"
                 position="bottom"
                 :style="{ height: '100%' }">
        <div class="pop-header">
          <nav-bar title="选择时间"
                   left-text="取消"
                   right-text="确定"
                   @click-left="cancel"
                   @click-right="done" />
        </div>
        <div class="pop-content">
          <div class="title">
            <span v-if="showTimeSelectPopup">按日选择</span>
            <span v-if="showDateSelectPopup">按月选择</span>
            <img :src="`${$cdnImageUrl}/mine/sales/change.png`"
                 alt="">
          </div>
          <div v-if="showTimeSelectPopup"
               class="date">
            <span class="dater"
                  :class="{'no-select':selectOn===1}"
                  @click="selectOn=0">{{ startDate | dateFormat }}</span>
            <span>至</span>
            <span class="dater"
                  :class="{'no-select':selectOn===0}"
                  @click="selectOn=1">{{ endDate  | dateFormat}}</span>
          </div>
          <div v-if="showDateSelectPopup"
               class="date">
            <span>{{ monthDate | monthFormat }}</span>
          </div>
          <div class="picker">
            <!-- 因为datetime-picker的bug，多个pick共存时 不能使用v-show -->
            <template v-if="showTimeSelectPopup && selectOn===0">
              <van-datetime-picker v-model="startDate"
                                   :show-toolbar='false'
                                   type="date"
                                   title="选择年月日"
                                   :min-date="minDate"
                                   :max-date="endDate"
                                   :formatter="formatter" />
            </template>
            <template v-if="showTimeSelectPopup && selectOn===1">
              <van-datetime-picker v-model="endDate"
                                   :show-toolbar='false'
                                   type="date"
                                   title="选择年月日"
                                   :min-date="startDate"
                                   :max-date="maxDate"
                                   :formatter="formatter" />
            </template>
            <van-datetime-picker v-if="showDateSelectPopup"
                                 v-model="monthDate"
                                 :show-toolbar='false'
                                 type="year-month"
                                 :min-date="minDate"
                                 :max-date="maxDate"
                                 :formatter="formatter" />
          </div>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import { DropdownMenu, DropdownItem, NavBar } from 'vant'
import { getGoodsAmountList, myManageinfo } from '@/http/service/mine'
import utils from '@/utils/utils'

let dateTime = new Date()
dateTime = dateTime.setDate(dateTime.getDate() - 1)
dateTime = new Date(dateTime)

export default {
  components: {
    Header,
    DropdownMenu,
    DropdownItem,
    NavBar,
  },
  data() {
    return {
      // 参数状态
      identifyFlag: 0,
      fundState: 0, // 0全部1预计收益、2提现中、3已提现
      fundType: 2, // 1货款2佣金3平台费4手续费
      pageSize: 10,
      pageNo: 1,
      dateType: 0, // 0 全部  1按日 2按月
      preDateType: 0, // 取消时 恢复状态
      // UI 状态
      dataList: [],
      totalComminsion: {
        totalComminsionIn: 0,
        totalComminsionOut: 0,
      },
      loading: false,
      loadFinished: false,
      showTimeSelectPopup: false,
      showDateSelectPopup: false,
      showPopup: false,
      fundStateOptions: [
        { text: '全部', value: 0 },
        { text: '预计收益', value: 1 },
        { text: '提现中', value: 2 },
        { text: '已提现', value: 3 },
      ],
      timeOptions: [
        { text: '全部', value: 0, selected: true },
        { text: '按日选择', value: 1, selected: false },
        { text: '按月选择', value: 2, selected: false },
      ],
      // date相关状态
      minDate: new Date(2016, 0, 1),
      maxDate: new Date(),
      startDate: dateTime,
      endDate: new Date(),
      selectOn: 0, // 0:start  1: end
      monthDate: new Date(),
      dateQuery: {
        transactionTimeEnd: utils.dateFormat('YYYY-mm', new Date()),
      },
    }
  },
  computed: {
    dateTypeTitle() {
      let title = this.timeOptions.find((item) => item.selected).text
      if (title === '按月选择') {
        title = this.dateQuery.transactionTimeEnd
      }
      return title
    },
    fundTypeOptions() {
      const options = [
        { text: '佣金账单', value: 2 },
        { text: '奖励账单', value: 5 },
      ]
      if (Number(this.identifyFlag) >= 10 && Number(this.identifyFlag) < 20) {
        options.splice(1, 0, { text: '货款账单', value: 1 })
        options.splice(-1, 0, { text: '手续费', value: 4 })
      }
      options.push({ text: '设计师奖励', value: 13 })
      return options
    },
  },
  filters: {
    dateFormat(date) {
      return utils.dateFormat('YYYY-mm-dd', date)
    },
    monthFormat(date) {
      return utils.dateFormat('YYYY-mm', date)
    },
    feePrice(item) {
      return `${item.forb === '1' ? '+' : '-'}${item.fund ? item.fund.toFixed(2) : '0.00'}`
    },
    stateText(item) {
      const { fundState, hasStore } = item
      const status = fundState
      // 1中台清分，2工行清分，3解冻， 4转快捷通，5提现，6 退款
      let statusStr = '预计收益'
      if (isNaN(parseInt(status)) && typeof status === 'string') {
        return status
      }
      switch (Number(status)) {
        case 1:
        case 2:
        case 6:
          statusStr = '预计收益'
          break
        case 3:
          statusStr = hasStore ? '已提现' : '提现中'
          break
        case 4:
          statusStr = '提现中'
          break
        case 5:
          statusStr = '已提现'
          break
        default:
          statusStr = '预计收益'
      }
      return statusStr
    },
  },
  methods: {
    fetchData(isClear = false) {
      // 拦截
      if (![11, 12, 13, 14, 15, 16, 16, 22, 21, 31].includes(Number(this.identifyFlag))) {
        return
      }
      if (isClear) {
        this.dataList = []
        this.loadFinished = false
        this.pageNo = 1
      }
      const { pageSize, fundState, fundType, pageNo } = this
      const query = {
        ...this.dateQuery,
        pageSize,
        fundState,
        fundType,
        pageNo,
        rewardType: '1', // rewardType //奖励类型 1：金币 2：钻石 3：实品
      }
      getGoodsAmountList(query)
        .then((res) => {
          this.loading = false
          if (!res.data.data.list || res.data.data.list.length === 0) {
            this.loadFinished = true
          } else {
            this.dataList = this.dataList.concat(res.data.data.list)
          }

          this.totalComminsion = {
            totalComminsionIn: res.data.data.totalComminsionIn || 0,
            totalComminsionOut: res.data.data.totalComminsionOut || 0,
          }
        })
        .catch((e) => {
          console.log(e.message)
        })
    },
    onLoad() {
      this.pageNo += 1
      this.fetchData()
    },
    doShowTimeSelectPopup() {
      this.showTimeSelectPopup = true
      this.showPopup = true
    },
    doShowDateSelectPopup() {
      this.showDateSelectPopup = true
      this.showPopup = true
    },
    onConfirmDateType(index, item) {
      this.timeOptions.forEach((op) => {
        op.selected = false
      })
      this.timeOptions[index].selected = true
      if (item.value === 1) {
        this.doShowTimeSelectPopup()
      } else if (item.value === 2) {
        this.doShowDateSelectPopup()
      }
      this.dateType = item.value
      this.$refs.item.toggle()
    },
    cancel() {
      this.dateType = this.preDateType
      this.timeOptions.forEach((item) => {
        item.selected = false
      })
      this.timeOptions.find((item) => item.value === this.dateType).selected = true
      this.showPopup = false
    },
    done() {
      if (this.showTimeSelectPopup) {
        this.dateQuery = {
          transactionTimeStart: utils.dateFormat('YYYY-mm-dd', this.startDate),
          transactionTimeEnd: utils.dateFormat('YYYY-mm-dd', this.endDate),
        }
      } else if (this.showDateSelectPopup) {
        this.dateQuery = {
          transactionTimeStart: utils.dateFormat('YYYY-mm', this.monthDate),
          transactionTimeEnd: utils.dateFormat('YYYY-mm', this.monthDate),
        }
      }
      this.showPopup = false
      this.fetchData(true)
    },
    formatter(type, val) {
      if (type === 'year') {
        return `${val}年`
      } else if (type === 'month') {
        return `${val}月`
      }
      return val
    },
  },
  created() {
    if (this.$route.query) {
      this.fundState = this.$route.query.fundState !== undefined ? Number(this.$route.query.fundState) : 0
      this.fundType = this.$route.query.fundType !== undefined ? Number(this.$route.query.fundType) : 2
    }
    myManageinfo().then(({ data }) => {
      if (data.success && data.data) {
        this.identifyFlag = data.data.identifyFlag
        if (![11, 12, 13, 14, 15, 16, 16, 22, 21, 31].includes(Number(data.data.identifyFlag))) {
          this.$router.replace('/financialStationLocked')
        } else {
          this.fetchData(true)
        }
      }
    })
  },
  watch: {
    showPopup(value) {
      if (!value) {
        this.showTimeSelectPopup = false
        this.showDateSelectPopup = false
      }
    },
    dateType: {
      sync: true,
      handler(value, oldValue) {
        this.preDateType = oldValue
        if (value === 0) {
          this.dateQuery = {
            transactionTimeEnd: utils.dateFormat('YYYY-mm', new Date()),
          }
          this.fetchData(true)
        }
      },
    },
    fundState() {
      this.fetchData(true)
    },
    fundType(value) {
      // 手续费时 ，默认选择全部，无其他选项
      if (value === 4) {
        this.fundState = 0
      }
      this.fetchData(true)
    },
  },
}
</script>
